@charset "UTF-8";
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 1.8rem;
  font-weight: 300;
}

ul,
li {
  color: #000000;
  font-size: 1.5rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
.contenedor a {
  text-decoration: none !important;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

iframe {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  color: #000000;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2.1rem;
}
@media (min-width: 870px) {
  h2 {
    font-size: 2.4rem;
  }
}

h3 {
  font-size: 1.9rem;
  font-weight: 500;
}
@media (min-width: 870px) {
  h3 {
    font-size: 2.2rem;
  }
}

p {
  font-size: 1.2rem;
}
@media (min-width: 870px) {
  p {
    font-size: 1.5rem;
  }
}

.fecha {
  position: absolute;
  top: 0;
  right: 0;
  text-align: end;
}
.fecha p {
  font-size: 1.5rem;
  color: #ffffff;
  font-weight: 700;
  margin-right: 2rem;
}

.cont-boton {
  margin-top: 3rem;
  display: flex;
  justify-content: space-evenly;
}

.boton {
  background-color: #f4eefa;
  color: #6e0dc2;
  border: 1px solid #e9d5fa;
  padding: 0.9rem 2rem;
  font-size: 1.1rem;
  text-transform: uppercase;
  border-radius: 0.6rem;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.06);
}
.boton:hover {
  background-color: #f0e2fd;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
}
.boton:active {
  transform: scale(0.97);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}
.boton:focus {
  outline: none;
  border-color: #999;
}

.boton-agregar {
  background-color: #f4f4f6;
  color: #6e0dc2;
  border: 1px solid #d5d0dd;
  padding: 0.7rem 1.4rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 0.6rem;
  font-family: "Inter", sans-serif;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 3px 8px rgba(26, 115, 232, 0.08);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.6rem;
}
.boton-agregar:hover {
  background-color: #eae8ef;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.08);
}
.boton-agregar:active {
  transform: scale(0.96);
  box-shadow: 0 2px 6px rgba(26, 115, 232, 0.2);
}
.boton-agregar:focus {
  outline: none;
  border-color: #bbaadd;
}

.boton-grabar {
  background-color: #e9f8f0;
  color: #178b4f;
  border: 1px solid #b4e0ce;
  padding: 0.8rem 1.6rem;
  font-size: 1.1rem;
  text-transform: uppercase;
  border-radius: 0.6rem;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.05);
  display: inline-block;
  width: auto;
  white-space: nowrap;
}
.boton-grabar:hover {
  background-color: #d4f3e3;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.08);
}
.boton-grabar:active {
  transform: scale(0.97);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
.boton-grabar:focus {
  outline: none;
  border-color: #98d6b8;
}

.boton-salir {
  background-color: #fceeee;
  color: #c0392b;
  border: 1px solid #f5c6c6;
  padding: 0.8rem 1.6rem;
  font-size: 1.1rem;
  text-transform: uppercase;
  border-radius: 0.6rem;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.05);
  display: inline-block;
  width: auto;
  white-space: nowrap;
}
.boton-salir:hover {
  background-color: #f8d6d6;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.08);
}
.boton-salir:active {
  transform: scale(0.97);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
.boton-salir:focus {
  outline: none;
  border-color: #e0a7a7;
}

.edit-button {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  background-color: #e3f2fd;
  color: #1e88e5;
  border: 1px solid #bbdefb;
  border-radius: 0.6rem;
  padding: 0.5rem 0.8rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 8px rgba(30, 136, 229, 0.15);
}
.edit-button svg {
  pointer-events: none;
}
.edit-button:hover {
  background-color: #bbdefb;
  box-shadow: 0 6px 12px rgba(30, 136, 229, 0.25);
}
.edit-button:focus {
  outline: none;
  border-color: #90caf9;
}

.boton-icono-ver {
  background-color: #e2f0f9;
  color: #317b9b;
  border: 1px solid #bfdfea;
  padding: 0.4rem;
  border-radius: 0.5rem;
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.boton-icono-ver:hover {
  background-color: #d4e8f4;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
.boton-icono-ver:active {
  transform: scale(0.97);
}
.boton-icono-ver:focus {
  outline: none;
  border-color: #a3cfe0;
}
.boton-icono-ver svg {
  stroke: currentColor;
  width: 20px;
  height: 20px;
}

.btn-editar {
  background-color: #d2f5d0;
  border: 1px solid #91fc60;
  border-radius: 0.5rem;
  width: 26px;
  height: 26px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.btn-editar svg {
  width: 18px;
  height: 18px;
  stroke: #388e3c;
  stroke-width: 2;
}
.btn-editar:hover {
  background-color: #b5eab3;
}

.btn-eliminar {
  background-color: #f9d6d5;
  border: 1px solid #f5c6c6;
  border-radius: 0.5rem;
  width: 26px;
  height: 26px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.btn-eliminar svg {
  width: 18px;
  height: 18px;
  stroke: #d32f2f;
}
.btn-eliminar:hover {
  background-color: #f4bcbc;
}

.btn-logout {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background-color: #f8d7da; /* Pastel rojo/rosa */
  color: #721c24; /* Texto más oscuro */
  border: none;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.btn-logout svg {
  width: 18px;
  height: 18px;
}

.btn-logout:hover {
  background-color: #f1b0b7; /* Un poco más oscuro al pasar el mouse */
  transform: translateY(-1px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

.btn-logout:active {
  transform: translateY(0);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.btn-gestion {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, #b3e5fc, #c8e6c9);
  color: #333;
  padding: 8px 18px;
  border-radius: 0.6rem;
  font-size: 15px;
  font-weight: bold;
  text-decoration: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
  transition: all 0.25s ease-in-out;
}
.btn-gestion svg {
  stroke: #333;
}
.btn-gestion:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  background: linear-gradient(135deg, #81d4fa, #a5d6a7);
}

.password-button {
  background-color: #e0f7fa;
  color: #00838f;
  border: 1px solid #b2ebf2;
  border-radius: 0.6rem;
  padding: 0.2rem 0.4rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 8px rgba(142, 36, 170, 0.15);
}
.password-button svg {
  pointer-events: none;
  width: 20px;
  height: 20px;
}
.password-button:hover {
  background-color: #b2ebf2;
  box-shadow: 0 6px 12px rgba(142, 36, 170, 0.25);
}
.password-button:focus {
  outline: none;
  border-color: #ce93d8;
}

.permission-button {
  background-color: #fff9c4;
  color: #f9a825;
  border: 1px solid #fff59d;
  border-radius: 0.6rem;
  padding: 0.2rem 0.4rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 8px rgba(249, 168, 37, 0.15);
}
.permission-button svg {
  pointer-events: none;
}
.permission-button:hover {
  background-color: #fff59d;
  box-shadow: 0 6px 12px rgba(249, 168, 37, 0.25);
}
.permission-button:focus {
  outline: none;
  border-color: #fdd835;
}

/* Estilo específico (opcional) si quieres un borde sutil */
.btn-pass {
  border: 1px solid rgba(110, 13, 194, 0.06);
}

.header img {
  position: relative;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}

.principal {
  margin: 1rem 1.5rem 2rem 1rem;
}

.tablas {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tablas table {
  max-width: 100%;
  justify-self: center;
  border-spacing: 1;
}
.tablas table th,
.tablas table td {
  text-align: center;
  padding: 0.2rem;
}
.tablas table .vb-cell {
  cursor: pointer;
  position: relative;
}
.tablas .table-left {
  justify-self: start;
}
.tablas p {
  margin: 0;
  text-align: start;
  font-size: 1.3rem;
}
.tablas h3,
.tablas h2,
.tablas h1 {
  justify-self: center;
  margin: 1rem;
}
.tablas h3 {
  font-size: 1.4rem;
  text-transform: uppercase;
}

.menu-contextual {
  position: absolute;
  display: none;
  background: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  z-index: 9999;
  padding: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.menu-contextual svg {
  width: 30px;
  height: 30px;
  cursor: pointer;
  margin: 5px;
}

.diseño_tablas tr:nth-child(odd) {
  background-color: #faf5f5;
}
.diseño_tablas tr:nth-child(even) {
  background-color: #ede1f8;
}

.diseño_form tr:nth-child(odd) {
  background-color: #ffffff;
}
.diseño_form tr:nth-child(even) {
  background-color: #faf5f5;
}
.diseño_form tr:nth-child(even) svg {
  margin: 0.3rem 0 0 1rem;
  height: 1.5rem !important;
  width: 1.5rem !important;
}

.relative {
  position: relative;
}

.radio-group {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}
.radio-group .radio-item {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 1.5rem;
  background-color: #f4eefa;
  padding: 4px 10px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}
.radio-group .radio-item input[type=radio] {
  accent-color: #6e0dc2;
  cursor: pointer;
}
.radio-group .radio-item:hover {
  background-color: #e6e1ee;
}

.eliminar {
  margin: 2rem auto 0 auto;
}
.eliminar p {
  color: red;
  font-weight: bold;
}

.menu-barra {
  display: flex;
  justify-content: center;
  background-color: #f8f4fc; /* base muy clara */
  padding: 12px;
  border-radius: 12px;
  margin: 20px auto;
  max-width: 800px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  gap: 8px;
}

.menu-opcion {
  text-decoration: none;
  font-size: 15px;
  font-weight: 500;
  padding: 10px 20px;
  border-radius: 8px;
  transition: background 0.3s, transform 0.2s;
  color: #fff;
}

.pastel-lila {
  background-color: #bfa5e2; /* lila pastel */
}

.pastel-menta {
  background-color: #a7e3c4; /* verde menta pastel */
}

.pastel-durazno {
  background-color: #D6BFA5; /* durazno pastel */
}

.menu-opcion:hover {
  transform: translateY(-2px);
  filter: brightness(1.08);
}

@media (max-width: 600px) {
  .menu-barra {
    flex-direction: column;
    align-items: center;
  }
  .menu-opcion {
    width: 100%;
    text-align: center;
  }
}
.anulado {
  color: #ff0202;
  font-weight: 700;
}

